.detals-deal__title {
  font-family: "Gotham Pro Bl", "Arial", sans-serif;
  font-weight: 400;
  line-height: 16px;

  @media (min-width: $tablet-width) {
    font-family: $font-family;
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;
  }
}
.detals-deal__sub-text {
